import React from "react";
import Select from "../../../components/select";

class LocationAndCategory extends React.Component {
  state = {
    form: {
      category: null,
      location: ""
    },
    error: false
  };

  genderOptions = [
    {
      name: "Male",
      value: "M"
    },
    {
      name: "Female",
      value: "F"
    }
  ];

  changeGender = category => {
    const { form } = this.state;
    form.category = category.value;
    this.setState({ form });
  };

  change = e => {
    const { form } = this.state;
    form[e.target.name] = e.target.value;
    this.setState({ form });
  };

  moveNextPage = () => {
    const { form } = this.state;

    if (form.category === null || form.location === "") {
      this.setState({
        error: true
      });
    }
  };

  render() {
    const { form, error } = this.state;
    return (
      <div className="location-category">
        <h2 className="title">Location and category</h2>
        <p className="paragraph">
          Lorem ipsum dolor sit amet consectetur adipisicing elit. Atque sint
          aspernatur, voluptate adipisci ab qui laborum a veniam itaque error
          tempore sit impedit ullam veritatis? Atque nemo sequi cumque
          reiciendis.
        </p>
        <div className="tab-label">
          <label>
            <span>Category</span>
            <Select
              useValue
              placeholder="Choose category"
              options={this.genderOptions}
              onChange={this.changeGender}
              value={form.category}
            />
          </label>
        </div>
        <div className="tab-label">
          <label>
            <span>Location</span>
            <input
              type="text"
              placeholder="Choose location"
              name="location"
              value={form.location}
              onChange={this.change}
              className={error ? "G-invalid-input" : ""}
            />
          </label>
        </div>
        <div className="map">
          <iframe src="https://www.google.com/maps/embed?pb=!1m21!1m12!1m3!1d31304.609153503716!2d44.632343738194386!3d40.18243439629083!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!4m6!3e0!4m3!3m2!1d40.1987215!2d44.611183!4m0!5e0!3m2!1sen!2s!4v1572611618069!5m2!1sen!2s"></iframe>
        </div>
        <button className="G-button" onClick={this.moveNextPage}>
          Continue
        </button>
      </div>
    );
  }
}

export default LocationAndCategory;
